.site-nav-right {
  .navigation-ul, .navigation-btn {
    display: none;
  }
}

.nav-post-title-active .site-nav-right .nav {
  transform: none;
  opacity: inherit;
  visibility: inherit;
}

@media (max-width: 700px) {
  .site-nav-right {
      display: inherit;
  }
}

@media (max-width: 400px) {
  .site-nav-right {
      .social-link-fa:not(.nav-item-0) {
        display: none;
      }
  }
}
